@import url(https://fonts.googleapis.com/css2?family=Grandstander:wght@500&display=swap);
body{font-family:"Grandstander",cursive;width:100vw;height:100vh;margin:0px}.slides{width:65%;border-radius:20px;margin:auto}.slides .carousel{display:flex;align-items:center;background-image:url(/funny-quiz-client/static/media/img2.8d375172.png)}.slides .carousel p{font-size:5em;margin:auto;background:linear-gradient(to right, #000, white);-webkit-background-clip:text;background-clip:text;color:transparent;transition:all 5s}.slides .carousel p:hover{background:linear-gradient(to right, #0048A9, white);-webkit-background-clip:text;background-clip:text;color:transparent}@media only screen and (max-width: 768px){.slides{width:100%}.slides .react-slideshow-container{margin:20px}.slides .carousel p{font-size:2em;margin:auto}}
